<template>
  <!-- 审核弹窗 -->
  <el-dialog
    :close-on-click-modal="false"
    :show-close="false"
    :visible.sync="dialogVisible.trunoff"
    width="30%"
  >
    <div class="title_examine">
      {{title}}
      <img @click="colseFn" src="../../../assets/icons/esc.png" />
    </div>
    <el-form ref="form" class="examine" :model="form">
      <el-form-item>
        <el-radio-group v-model="form.checkStatus">
          <template v-if="title=='退定'">
                <el-radio label="退订不可以编辑"></el-radio>
                 <el-radio label="退定可以编辑"></el-radio>
          </template>
          <template v-else>
                <el-radio label="通过"></el-radio>
                <el-radio label="拒绝"></el-radio>
          </template>
         
        </el-radio-group>
      </el-form-item>
      <el-form-item label="原因:">
        <el-input type="textarea" v-model="form.reason"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="background-color: #0b8d7e;color:#fff;" @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
import { checkInvoice } from "@/api/business/commission";
import { checkDeptInvoice } from "@/api/business/management";
import { checkTicket } from "@/api/business/Picturepreview";
export default {
  props: {
    dialogVisible: {
      type: Object
    },
    type: {
      type: String
    },
    title:{
      type:String,
      default:'待审核'
    }
  },
  data() {
    return {
      form: {
        checkStatus: "",
        reason: ""
      }
    };
  },
  methods: {
    onSubmit() {
      let invoiceId = null;
      if (this.type === "checkDeptInvoice") {
      this.form.checkStatus = this.form.checkStatus === "通过" ? 3 : 4;
        invoiceId = this.$store.getters.invoiceId;
        let data = { ...this.form, invoiceId };
        checkDeptInvoice(data).then(res => {
          if (res.data.code === "000") {
            this.$message.success("审核完成");
            this.colseFn();
          }
          if (res.data.code === "201") {
            this.$message.error(res.data.msg);
            this.colseFn();
          }
        });
      } else if (this.type === "Picturepreview") {
        //成交单审核
        this.form.checkStatus = this.form.checkStatus === "通过" ? 3 : 4;
        let ticketId = this.$store.getters.ticlkdeil.ticketId;
        let isCheck = 0;
        if (ticketId == 3) {
          isCheck = 1;
        }
        let data = { ...this.form, ticketId, isCheck };
        checkTicket(data).then(res => {
          if (res.data.code === "000") {
            this.$message.success("审核通过");
            this.colseFn()
          } else {
            this.$confirm(res.data.msg+", 是否强制提交?", "提示", {
              confirmButtonText: "是",
              cancelButtonText: "否",
              type: "warning"
            })
            .then(()=>{
                data.isCheck=0
                checkTicket(data).then(res=>{
                  if(res.data.code==='000'){
                    this.$message.success('审核通过')
                    this.colseFn()
                  }
                })
            })
          }
        });
      } else if(this.type==='Picturepreviews'){//成交单退定
           let ticketId = this.$store.getters.ticlkdeil.ticketId;
           this.form.checkStatus = this.form.checkStatus === "退订不可以编辑" ? 5 : 6;
           let data = { ...this.form, ticketId};
            checkTicket(data).then(res=>{
              if(res.data.code==='000'){
                this.$message.success('退定成功')
                this.colseFn()
              }
            })
      }else{
        this.form.checkStatus = this.form.checkStatus === "通过" ? 3 : 4;
        invoiceId = this.$parent.$parent.invoiceId;
        let data = { ...this.form, invoiceId };
        checkInvoice(data).then(res => {
          if (res.data.code === "000") {
            this.$message.success("审核完成");
            this.colseFn();
          }
          if (res.data.code === "201") {
            this.$message.error(res.data.msg);
            this.colseFn();
          }
        });
      }
    },
    colseFn() {
      this.$parent.$parent.onSubmit();
      this.$parent.$parent.handleClick();
      this.dialogVisible.trunoff = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.examine {
  .el-form-item:nth-child(1),
  .el-form-item:nth-child(3) {
    margin-bottom: 0px;
    text-align: center;
  }
}

/deep/.el-dialog {
  position: relative;
  border-radius: 6px;
  .el-dialog__header {
    padding: 0;
  }

  .title_examine {
    text-align: center;
    width: 100%;
    font-size: 24px;
    color: #000;
    img {
      position: absolute;
      top: 0;
      right: 10px;
    }
  }
  .el-dialog__body {
    padding: 10px 30px;
  }
}
</style>