<template>
  <!-- 图片预览组件 -->
  <el-dialog
     :visible.sync="dialogVisible.trunoff"
     width="30%"
     top="10px"
     :before-close="handleClose">
      <img style="width:100%;height:100%;" :src="dialogVisible.urlSrc" alt="">
</el-dialog>

</template>

<script>
  export default {
    props:{
        dialogVisible:{
            type:Object
        },
    },
    data() {
      return {
      };
    },
    methods: {
      handleClose(done) {
            done();
      }
    }
  };
</script>