<template>
  <div class="addPic">
    <el-dialog
      class="addPic_top"
      title="业绩单录入"
      :visible.sync="dialogVisible.trunoff"
      width="86%"
      :before-close="handleClose"
      top="10px"
    >
      <el-card>
        <el-form :rules="rules" ref="fromData" :model="fromData" label-width="120px">
          <el-row>
            <el-col :span="8">
              <el-form-item prop="signTime" label="草签日期">
                <el-date-picker
                  style="width:100%;"
                  v-model="fromData.signTime"
                  type="date"
                  placeholder="选择日期"
                ></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="commCase" label="佣金方案">
                <el-input style="width:100%;" v-model="fromData.commCase" placeholder="佣金方案" />
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="kfsSumCommission" label="总创佣">
                <el-input
                  style="width:100%;"
                  v-model="fromData.kfsSumCommission"
                  placeholder="请输入总创佣"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="kfsCommCase" label="门店佣金方案">
                <el-input
                  style="width:100%;"
                  v-model="fromData.kfsCommCase"
                  placeholder="请输入门店佣金方案"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="preCommission" label="门店前用">
                <el-input
                  style="width:100%;"
                  v-model.number="fromData.preCommission"
                  placeholder="请输入门店前用"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="sufCommission" label="门店后佣">
                <el-input
                  style="width:100%;"
                  v-model="fromData.sufCommission"
                  placeholder="请选择门店后佣"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="cash" label="现金奖">
                <el-input style="width:100%;" v-model.number="fromData.cash" placeholder="请输入现金奖"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="payCash" label="现金奖已支付">
                <el-switch style="width:100%;" v-model="fromData.payCash"></el-switch>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="revenue" label="营收">
                <el-input style="width:100%;" v-model.number="fromData.revenue" placeholder="请输入营收"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="业绩凭证">
                <upPictrue ref="upPictrueTwo"/>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="备注">
                <el-input style="width:100%;" type="textarea" v-model="fromData.remark"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-form-item>
              <el-button style="float: right;" type="primary" @click="submitForm">保存</el-button>
            </el-form-item>
          </el-row>
        </el-form>
        <!-- <el-form label-width="100px" inline="inline">
          <el-form-item v-for="(item,index) in topSuFn" :key="index" style="width:300px;" :label="item.title">
               <templa style="width:100%;" te v-if="item.text!=null&&(item.key=='picUrls'||item.key=='eleUrls')">
               <img style="width:100px;height:80px;" v-for="(i) in item.text" :key="i" :src="i"/>
               </template>
               <template v-else>
                   {{item.text}}
               </template>
          </el-form-item>
        </el-form>-->
      </el-card>
    </el-dialog>
  </div>
</template>
<script>
// import { deptList } from "@/api/business/management";
// import { userList } from "@/api/business/commission";
import {
  editAchieveTicket,
  commitAchieveTicket
} from "@/api/business/Picturepreview";
import { validatePass, dateTime } from "@/utils/common";
import upPictrue from "@/components/common/upPictrue";
export default {
  components: {
    upPictrue
  },
  props: {
    dialogVisible: {
      type: Object
    }
  },
  data() {
    return {
      topSucc: [],
      rules: {
        revenue: [
          {
            required: true,
            message: "请输入营收",
            type: "number",
            trigger: "blur"
          },
          {validator: validatePass,trigger:'change'}
        ],
        kfsSumCommission: [
          {
            required: true,
            message: "请输入总创佣",
            trigger: "blur"
          },
          {validator: validatePass,trigger:'change'}
        ],
        kfsCommCase: [
          { required: true, message: "请输入门店佣金方案", trigger: "blur" }
          // {validator: validatePass,trigger:'change'}
        ],
        cash: [
          { required: true, message: "现金奖", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
          // {validator: validatePass,trigger:'change'}
        ],
        sufCommission: [
          { required: true, message: "门店后佣", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
          // {validator: validatePass,trigger:'change'}
        ],
        preCommission: [
          { required: true, message: "门店前用", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
          // {validator: validatePass,trigger:'change'}
        ],
        cycleDays: [
          { required: true, message: "请输入团购费实收", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
          // {validator: validatePass,trigger:'change'}
        ],
        signTime: [
          { required: true, message: "选择日期", trigger: "blur" }
          // {validator: validatePass,trigger:'change'}
        ],
        commCase:[
           { required: true, message: "请输入佣金方案", trigger: "blur" }
        ]
      },
      fromData: {
        ticketId: "",
        signTime: "",
        // cycleDays:'',
        preCommission: "",
        sufCommission: "",
        cash: "",
        sumCommission: "",
        revenue: "",
        picUrl2: [],
        kfsSumCommission: "",
        commCase: "",
        payCash: "",
        remark: ""
      }
    };
  },
  methods: {
    handleClose(done) {
      done();
    },
    // 保存
    submitForm() {
      this.$refs.fromData.validate(valid => {
        if (valid) {
          let data = JSON.parse(JSON.stringify(this.fromData));
          data.payCash = data.payCash ? 1 : 0;
          data.signTime = dateTime(data.signTime, 0);
          data.ticketId = this.$store.getters.ticlkdeil.ticketId;
          data.picUrl2= this.$refs.upPictrueTwo.urls  //业绩凭证
          console.log(data)
          editAchieveTicket(data).then(res => {
            if (res.data.code === "000") {
              this.$confirm("已保存, 是否提交?", "提示", {
                confirmButtonText: "是",
                cancelButtonText: "否",
                type: "warning"
              }).then(() => {
                commitAchieveTicket({
                  ticketId: res.data.bizData.ticketId
                }).then(res => {
                  if (res.data.code === "000") {
                    this.$message.success("提交成功");
                    this.resetFrom()
                  }
                });
              })
              .catch(()=>{
                this.resetFrom()
              })
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //重置
    resetFrom() {
      this.$parent.$parent.onSubmit();
      this.$parent.$parent.handleClick(this.$store.getters.tickta)
      this.$refs.fromData.resetFields();
      this.dialogVisible.trunoff = false;
    }
  },
  computed: {
    topSuFn() {
      let data = [
        ...this.dialogVisible.data[0].one,
        ...this.dialogVisible.data[0].two
      ];
      return data;
    }
  },
  watch: {
    dialogVisible: {
      handler() {
        console.log(this.dialogVisible);
        // this.topSucc = [...this.succData.one, ...this.succData.two];
        // console.log(this.topSucc)
        // let tol = Math.ceil(data.length / 3);
        // let copy=[]
        // for (let i = 0; i < tol; i++) {
        //   let obj = {};
        //   obj.arr = [];
        //   data.forEach((item, index) => {
        //     if (index % 3 || index == 0) {
        //       obj.arr.push(item);
        //     }
        //   });

        // }
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
.addPic .addPic_top .el-dialog {
  background-color: rgb(245, 241, 241) !important;
  .el-dialog__body {
    padding-top: 0;
  }
}
</style>