<template>
  <div class="yulan">
    <div class="contex_yulan" v-for="(item,index) in urls" :key="item">
      <img class="upimg" :src="item" />
      <i class="el-icon-delete icons_img" @click="remove(index)"></i>
    </div>
    <div class="head-img">
      <img src="../../assets/icons/上传icon.png"  @click="upLoad" id="my-img" />
      <input type="file" id="img-upload" />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    imgUrl: {
      type: Array
    }
  },
  data() {
    return {
      urls: this.imgUrl || [],
      // copyusrl:[]
    };
  },
  methods: {
    upLoad() {
      let imgs = document.getElementById("img-upload");
      imgs.click();
      var that = this;
      imgs.onchange = function(e) {
        //创建读取文件的对象
        const reader = new FileReader();
        //使用该对象读取file文件
        reader.readAsDataURL(e.target.files[0]);
        // let copy = URL.createObjectURL(e.target.files[0]);
        // that.urls.push(copy);
        // 调用上传
        that.Oss.uploadFile(e.target.files[0]).then(res => {
          if(res.code===0&&res.msg=='ok'){
              that.urls.push(res.url)
              // that.copyusrl.push(e.target.files[0])
          }
        }).catch(err=>{
          console.log(err)
        })
        // console.log(e.target.files[0])
        //读取文件成功后执行的方法函数
        reader.onload = function(e) {
          //读取成功后返回的一个参数e，整个的一个进度事件
          //选择所要显示图片的img，要赋值给img的src就是e中target下result里面
          //的base64编码格式的地址
          // console.log( e)
          // that.urls.push(e.target.result) ;
        };
      };
    },
    remove(index) {
      // this.urls.splice(index, 1);
      // this.Oss.deleteFile(this.copyusrl[index]).then(res=>{
        // this.copyusrl.splice(index,1)
        this.urls.splice(index, 1);
      // })
      
    },
    //创建oss存储空间
    creatFnOss() {
      let OSS = require("ali-oss");

      let client = new OSS({
        region: "<Your region>",
        accessKeyId: "LTAI4Fdj9VxsTXtVFCMWQNv9",
        accessKeySecret: "n9l1Uc9yRRmvbTywfEeT53o9EEUBz9"
      });

      // putBucket
      async function putBucket() {
        try {
          const result = await client.putBucket("your bucket name");
          console.log(result);
        } catch (err) {
          console.log(err);
        }
      }

      putBucket();
    }
  }
};
</script>
<style lang="scss" scoped>
#img-upload {
  display: none;
}
.contex_yulan {
  width: 120px;
  height: 130px;
  position: relative;
}
.yulan {
  display: flex;
}
.icons_img {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 22px;
  cursor: pointer;
  z-index: 100;
}
.upimg {
  display: inline-block;
  width: 100%;
  height: 100%;
}
</style>